import React from 'react';

import Form from '../Form/Form';
import exit from '../../assets/images/exit-white.svg';
import './Backdrop.css';

const Backdrop = ({ show, click, showForm }) => {
    let backdropClasses = 'hide'
    if (show) {
        backdropClasses = 'backdrop sticky show';
    }

    const onClick = (event) => {
        if (event.target === event.currentTarget) {
            click();
        }
    }

    return (
        <div className={backdropClasses} onClick={onClick}>
            <Form showForm={ showForm } complete={click} />
            <img className="close-backdrop pointer" src={exit} alt="Выйти" onClick={click} />
        </div>
    );
}

export default Backdrop;