import React from 'react';
import { useSpring, animated } from 'react-spring';

import hse from '../../assets/images/hse-blue.svg';
import nauchka from '../../assets/images/nauchka.svg';
import seiran from '../../assets/images/seiran.svg';
import './PartnerCard.css';

const partners = [
    {
        img: hse,
        site: 'https://oriental.hse.ru/',
        name: 'Школа востоковедения',
        style: 'partner-logo-1',
        desc: 'Школа востоковедения является одним из ведущих российских центров в области преподавания востоковедения и изучения Восточной Азии и Ближнего Востока'
    },
    {
        img: nauchka,
        site: 'http://nauchka.ru/',
        name: 'Научка',
        style: 'partner-logo-2',
        desc: 'Первая научно-популярная библиотека, где вас ждет серия научных, популярных и научно-популярных форматов, отличные книги и интеллектуальная компания'
    },
    {
        img: seiran,
        site: 'https://seiran.school/',
        name: 'SEIRAN',
        style: 'partner-logo-3',
        desc: 'Школа японского языка в Москве, где можно пообщаться с носителями японского языка, узнать больше о культуре и современной жизни в Японии, а также попробовать свои силы, подготовившись к Норёку сикэн'
    },
];

const PartnerCard = ({ num }) => {

    const [props, set] = useSpring(() => ({ s: 1, config: { mass: 5, tension: 350, friction: 40 } }));
    return <animated.div
        onMouseEnter={ () => set({ s: 1.1 }) }
        onMouseLeave={ () => set({ s: 1 }) }
        style={{ transform: props.s.interpolate((s) => `scale(${s})`) }}
        className="partner-card border-radius text-centered line-height"
    >
        <a href={partners[num].site}>
            <div className="partner-logo-cnt container pointer">
                <img src={partners[num].img} alt={partners[num].name} className={partners[num].style} />
            </div>
        </a>
        <hr />
        <div className="partner-info">
            <h2>{partners[num].name}</h2>
            <p>{partners[num].desc}</p>
        </div>
    </animated.div>;
}

export default PartnerCard;