import React, { useState, useContext, useEffect, useRef } from 'react';
import { useSpring, useTransition, animated, config, useChain } from 'react-spring';

import { CardStatesContext } from '../../store/CardStatesStore';
import { InputFieldsContext } from '../../store/InputFieldsStore';
import Event from './Event';
import './Events.css';

// const pages = [
//     ({ style, arrowClicked }) => <animated.div style={style} className="relative">
//         <Event
//             dayNum={0}
//             arrowClicked={arrowClicked} />
//     </animated.div>,
