const getChain = (transRef1, transRef2, transRef3, day, prevDay) => {
    if ((day == 0) && (prevDay == 1)) {
        return [transRef2, transRef1];
    } else if ((day == 0) && (prevDay == 2)) {
        return [transRef3, transRef1];
    } else if ((day == 1) && (prevDay == 0)) {
        return [transRef1, transRef2];
    } else if ((day == 1) && (prevDay == 2)) {
        return [transRef3, transRef2];
    } else if ((day == 2) && (prevDay == 0)) {
        return [transRef1, transRef3];
    } else if ((day == 2) && (prevDay == 1)) {
        return [transRef2, transRef3];
    } else return [];
}

const Events = () => {

    // const [eventDayOpened, setEventDayOpened] = useState(-1);
    const [eventProperties, setDay] = useContext(CardStatesContext);

    const transRef1 = useRef()
    const transRef2 = useRef()
    const transRef3 = useRef()

    // useChain(getChain(transRef1, transRef2, transRef3, eventProperties.day, eventProperties.prevDay));

    // useChain(eventDayOpened == 0 ? [transRef2, transRef3, transRef1] : eventDayOpened == 1 ? [transRef1, transRef3, transRef2] : [transRef2, transRef1, transRef3], [0, 0, .4]);
    useChain(eventProperties.day == 0 ? [transRef2, transRef3, transRef1] : eventProperties.day == 1 ? [transRef1, transRef3, transRef2] : [transRef2, transRef1, transRef3]);

    const [inputValues, onNameChange, onEmailChange] = useContext(InputFieldsContext);

    let firstDayStyle = 'events-header pointer';
    if (eventProperties.day === 0) {
        firstDayStyle = 'events-header grad-bg pointer white';
    }
    let secondDayStyle = 'events-header pointer';
    if (eventProperties.day === 1) {
        secondDayStyle = 'events-header grad-bg pointer white';
    }
    let thirdDayStyle = 'events-header pointer';
    if (eventProperties.day === 2) {
        thirdDayStyle = 'events-header grad-bg pointer white';
    }

    return (
    <div className="relative overflow-hidden full-width margin-from-prev-comp">
        <h1 className="register-header text-centered">Регистрация</h1>
        <div className="container" style={{marginBottom: '1.5em'}}>
                <div className="flex-column input-div">
                    <label>Введите имя и фамилию</label>
                    <input type="text" placeholder="Как вас зовут" className="sign-input border-radius" onChange={onNameChange} value={inputValues.name} required/>
                </div>
                <div className="flex-column input-div">
                    <label>Введите электронную почту</label>
                    <input type="text" placeholder="Ваша почта" className="sign-input border-radius" onChange={onEmailChange} value={inputValues.email} required/>
                </div>
        </div>

        <div className="flex ev-cards">
            <div className={firstDayStyle} onClick={() => setDay(0)} >
                <h2 className="text-centered">Открытие</h2>
                <p className="text-centered">Воскресенье<br />27 Октября 2019</p>
            </div>
            <div className={secondDayStyle} onClick={() => setDay(1)} >
                <h2 className="text-centered">Первый день</h2>
                <p className="text-centered">Суббота<br />3 Ноября 2019</p>
            </div>
            <div className={thirdDayStyle} onClick={() => setDay(2)} >
                <h2 className="text-centered">Последний день</h2>
                <p className="text-centered">Воскресенье<br />3 Ноября 2019</p>
            </div>
        </div>

        <Event dayNum={0} transRef={transRef1} />
        <Event dayNum={1} transRef={transRef2} />
        <Event dayNum={2} transRef={transRef3} />

        <button>Записаться</button>
    </div>
    );
}

export default Events;