import React from 'react';

import PartnerCard from './PartnerCard';
import './Partners.css';

const Partners = () => {
    
    return <div className="partners">
        <h2>Наши партнеры:</h2>
        <ul className="hs">
            <li><PartnerCard num={0} /></li>
            <li><PartnerCard num={1} /></li>
            <li><PartnerCard num={2} /></li>
        </ul>
    </div>;
}

export default Partners;