import React, { createContext, useState, useEffect } from 'react';

const WindowPropertiesContext = createContext();
export { WindowPropertiesContext };

// function reducer(state, action) {
//     switch(action.type) {
//         case 'UPDATE_WINDOW_SIZE':
//             return {...state, width: action.payload, height: action.payload };
//         default:
//             throw Error('Reducer error');
//     }
// }

export const WindowPropertiesProvider = props => {

    const [windowProperties, setWindowProperties] = useState({ width: window.innerWidth, navVisible: true });
    const [prevScrollpos, setPrevScrollpos] = useState(window.pageYOffset);

    const updateWindowDimensions = () => {
        setWindowProperties({ width: window.innerWidth, navVisible: windowProperties.navVisible });
    }
    const handleScroll = () => {
        const currentScrollPos = window.pageYOffset;
        const visible = prevScrollpos > currentScrollPos;
        setPrevScrollpos(currentScrollPos);
        setWindowProperties({ width: windowProperties.width, navVisible: visible })
    };

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        window.addEventListener('resize', updateWindowDimensions);
        return () => {
            window.removeEventListener('scroll', handleScroll);
            window.removeEventListener('resize', updateWindowDimensions);
        }
    });

    return (
        <WindowPropertiesContext.Provider value={[windowProperties, setWindowProperties]}>
            { props.children }
        </WindowPropertiesContext.Provider>
    );
}