import React, { useState, useEffect, useRef } from 'react';
import { useSpring, animated } from 'react-spring';
import ResizeObserver from 'resize-observer-polyfill';

import './Faq.css';

const questionsAnswers = [
    {
        id: 0,
        question: 'Куда я попал?',
        answerHeight: 29,
        answer: <p className="desktop-font line-height">Юкарихон: стрелы и строфы» — это городская школа японистики в Москве, посвящённая Средневековой Японии XII-XVI веков.</p>,
    },
    {
        id: 1,
        question: 'Что будет происходить?',
        answerHeight: 98,
        answer: <div>
            <p className="desktop-font line-height">27 октября — открытие Школы, выступление реконструкторов, открытая дискуссия «Японист в пост-миллениум».<br />2-3 ноября — вака-баттл, кинопоказ, встреча разговорного клуба, лекции, интервью, беседы, мастер-классы, закрытие Школы.</p>
            <p className="desktop-font line-height">Суммарно будет проведено 30 мероприятий, из них 9 — беседы, открытые встречи деятельных японистов с аудиторией, в ходе которых ведущие расскажут о собственном пути к изучению Японии — лайфхаках и особенностях познания в той или иной области.</p>
        </div>,
    },
    {
        id: 2,
        question: 'Зачем это мне?',
        answerHeight: 112,
        answer: <ul className="desktop-font line-height">
            <li>получить знания о Японии, ранее доступные только в ВУЗах, сломить стереотипы и углубить понимание;</li>
            <li>увидеть японистику по-разному — познакомиться с блоггерами, игротехниками, писателями и мастерами искусств;</li>
            <li>понять наконец, что можно делать с образованием япониста;</li>
            <li>найти единомышленников с горящими глазами и вписаться в японоведческий проект;</li>
            <li>попробовать себя в различных областях прикладной японистики.</li>
        </ul>,
    },
    {
        id: 3,
        question: 'Как участвовать?',
        answerHeight: 96,
        answer: <p className="desktop-font line-height">Во-первых, бесплатно и без возрастных ограничений.<br />Во-вторых, в библиотеке «Научка» по адресу ул. Дубининская, д. 20 (5 минут от метро «Павелецкая») или дистанционно.<br />В-третьих, места для очного участия ограничены, поэтому записывайтесь заранее.<br />Для активных участников приготовлены призы.</p>,
    },
    {
        id: 4,
        question: 'Кто организует?',
        answerHeight: 51,
        answer: <p className="desktop-font line-height">Команда «Юкарихон» — 20 молодых и амбициозных японистов, дизайнеров и маркетологов, выступающие в качестве посредников между различными сообществами российской японистики.</p>,
    },
];

const Faq = ({ buttonClick }) => {

    const refs = [useRef(), useRef(), useRef(), useRef(), useRef()];
    const [questionOpened, setQuestionOpened] = useState(-1);
    const [buttonHovered, setButtonHovered] = useState(false);
    const [height0, setHeight0] = useState(0);
    const [height1, setHeight1] = useState(0);
    const [height2, setHeight2] = useState(0);
    const [height3, setHeight3] = useState(0);
    const [height4, setHeight4] = useState(0);

    useEffect(() => {
        const ro = [
            new ResizeObserver(([entry]) => {
                setHeight0(entry.target.scrollHeight);
            }),
            new ResizeObserver(([entry]) => {
                setHeight1(entry.target.scrollHeight);
            }),
            new ResizeObserver(([entry]) => {
                setHeight2(entry.target.scrollHeight);
            }),
            new ResizeObserver(([entry]) => {
                setHeight3(entry.target.scrollHeight);
            }),
            new ResizeObserver(([entry]) => {
                setHeight4(entry.target.scrollHeight);
            }),
        ];

        refs.forEach((ref, i) => {
            if (ref.current) {
                ro[i].observe(ref.current)
            }
        })
    
        return () => {
            ro.forEach(r => r.disconnect());
        }
    }, [refs]);

    const propsArray = [
        useSpring({
            from: { opacity: 0, height: 0 },
            to: { opacity: (questionOpened === 0) ? 1 : 0, height: (questionOpened === 0) ? height0 : 0 },
        }),
        useSpring({
            from: { opacity: 0, height: 0 },
            to: { opacity: (questionOpened === 1) ? 1 : 0, height: (questionOpened === 1) ? height1 : 0 },
        }),
        useSpring({
            from: { opacity: 0, height: 0 },
            to: { opacity: (questionOpened === 2) ? 1 : 0, height: (questionOpened === 2) ? height2 : 0 },
        }),
        useSpring({
            from: { opacity: 0, height: 0 },
            to: { opacity: (questionOpened === 3) ? 1 : 0, height: (questionOpened === 3) ? height3 : 0 },
        }),
        useSpring({
            from: { opacity: 0, height: 0 },
            to: { opacity: (questionOpened === 4) ? 1 : 0, height: (questionOpened === 4) ? height4 : 0 },
        })
    ];

    const hoveredContactProps = useSpring({
        from: { left: (buttonHovered) ? '-150%' : '50%' },
        to: { left: (buttonHovered) ? '50%' : '-150%' },
        config: {
            duration: 200,
        }
    });

    const notHoveredContactProps = useSpring({
        from: { transform: (buttonHovered) ? 'translate3d(0%,0,0)' : 'translate3d(150%,0,0)' },
        to: { transform: (buttonHovered) ? 'translate3d(150%,0,0)' : 'translate3d(0%,0,0)' },
        config: {
            duration: 200,
        }
    });


    const handleQuestionClick = (questionNum) => {
        if (questionOpened === questionNum) {
            setQuestionOpened(-1)
        } else {
            setQuestionOpened(questionNum)
        }
    }

    const getQuestionStyle = (questionNum) => (questionNum === questionOpened) ? 'question-opened question pointer' : 'pointer question';

    const createQuestionAnswer = (questionNum) => {
            return <div>
                <p onClick={() => handleQuestionClick(questionNum)} className={getQuestionStyle(questionNum)}>{questionsAnswers[questionNum].question}</p>
                <animated.div className="answer" style={propsArray[questionNum]}>
                    <div ref={refs[questionNum]}>{questionsAnswers[questionNum].answer}</div>
                </animated.div>
            </div>
    };

    const contactButtonText = () => {
        return <div className="relative">
            <animated.div style={hoveredContactProps} className="absolute ask-question-text">Задайте свой вопрос!</animated.div>
            <animated.div style={notHoveredContactProps}>Не нашли нужный ответ?</animated.div>
        </div>;
    }

    return (
        <div className="padded-container faq-container margin-from-prev-comp">
            <h1 className="text-centered">Часто задаваемые вопросы</h1>
            {createQuestionAnswer(0)}
            <hr />
            {createQuestionAnswer(1)}
            <hr />
            {createQuestionAnswer(2)}
            <hr />
            {createQuestionAnswer(3)}
            <hr />
            {createQuestionAnswer(4)}
            <hr />
            <div className="contact-us-cnt">
                <div className="contact-us text-centered pointer"
                    onMouseEnter={() => setButtonHovered(true)}
                    onMouseLeave={() => setButtonHovered(false)}
                    onClick={buttonClick}>
                    {contactButtonText()}
                </div>
            </div>
        </div>
    );
}

export default Faq;