export const CardStatesProvider = props => {

    const [eventProperties, setEventProperties] = useState({ day: -1, prevDay: -1, group: 0, openedCard: { groupNum: -1, cardNum: -1 },
        checkedCards: [new Array(events[0].lectures.length).fill(-1),
        new Array(events[1].lectures.length).fill(-1),
        new Array(events[2].lectures.length).fill(-1)] });

    const setDay = (selectedDay) => {
        setEventProperties({ ...eventProperties, openedCard: { groupNum: -1, cardNum: -1 }, prevDay: eventProperties.day === selectedDay ? -1 : eventProperties.day, day: eventProperties.day === selectedDay ? -1 : selectedDay });
    }

    const setGroup = (selectedGroup) => {
        setEventProperties({ ...eventProperties, group: selectedGroup });
    }

    const setOpenedCard = (grNum, cNum) => {
        setEventProperties({ ...eventProperties, openedCard: { groupNum: grNum, cardNum: cNum } });
    }

    return <CardStatesContext.Provider value={[eventProperties, setDay, setGroup, setOpenedCard]}>
        { props.children }
    </CardStatesContext.Provider>

}