import React from 'react';

import './About.css';

const About = () => {
    return (
        <div className="container">
            <div className="w-c">
                <div className="wave-cnt" />
                <div className="wave-container relative grad-bg">
                    <div>
                        <ul className="desktop-font line-height">
                            <li>
                                <p className="white">Самураи не служат Императору, Фудзиямы не существует, а сакура не цветёт круглый год</p>
                            </li>
                            <li>
                                <p className="white">Впервые профессиональные японисты выйдут на открытую дискуссию с любителями.<br />Впервые успешные исследователи поделятся лайфхаками с участниками</p>
                            </li>
                            <li>
                                <p className="white">Российская японистика заявляет: мы готовы рассказать о вопросах сложных, но чертовски интересных</p>
                            </li>
                            <li>
                                <p className="white">30 мероприятий о Средневековой Японии XII-XVI веков.<br />Вака-баттл, кинопоказ, интервью, выступление реконструкторов, публичная дискуссия и другие</p>
                            </li>
                            <li>
                                <p className="white">Бесплатно для всех участников.<br />Возможно дистанционное участие</p>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default About;