import React, { createContext, useState } from 'react';

const InputFieldsContext = createContext();
export { InputFieldsContext };

export const InputFieldsProvider = props => {

    const [inputValues, setInputValues] = useState({name: '', email: ''});
    const onNameChange = (event) => {

        let inputName = event.target.value;
        setInputValues( {name: inputName, email: inputValues.email })
        // if (inputName.length > 1) {
        //     setInputValues( {name: inputName, email: inputValues.email })
        // } else {
        //     setInputValues( {name: '', email: inputValues.email })
        // }
    }

    const onEmailChange = (event) => {
        let inputEmail = event.target.value;
        setInputValues( {name: inputValues.name, email: inputEmail })
        // let re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        // if (re.test(inputEmail)) {
        //     setInputValues( {name: inputValues.name, email: inputEmail })
        // } else {
        //     setInputValues( {name: inputValues.name, email: '' })
        // }
    }

    return <InputFieldsContext.Provider value={[inputValues, onNameChange, onEmailChange]}>
        { props.children }
    </InputFieldsContext.Provider>

}